.filters {
  display: flex;
  align-items: center;
  height: 40px;
  margin: 0 -8px var(--gap);

  @include laptop() {
    flex-direction: column;
    height: auto;
    align-items: flex-start;
    margin: 0 0 var(--gap);
  }

  &__col {
    flex: 1 1 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 var(--gap);

    @include laptop() {
      align-self: stretch;
      margin: 0;
    }

    &--315 {
      flex: 0 0 315px;

      @include laptop() {
        flex: 0 0 auto;
        margin-bottom: var(--gap);
      }
    }
  }
}
