@media screen and (max-width: 1600px) {
  .container {
    max-width: 1264px;
  }

  .main-slider {
    height: 188px;
  }

  .main-slider__img {
    width: 235px;
    height: 180px;
    margin-right: calc(var(--gap) * 3);
  }

  .main-slider__content {
    padding: calc(var(--gap) * 3);
    padding-right: calc(var(--gap) * 12);
  }

  .main-slider__title {
    font-size: 32px;
    line-height: 38px;
  }

  .main-slider__text {
    font-size: 16px;
    line-height: 20px;
  }

  .main-slider__arrow {
    width: 16px;
    height: 16px;
  }

  .main-slider .slick-dots li {
    width: 16px;
    height: 16px;
  }

  .main-slider .slick-dots li button {
    width: 8px;
    height: 8px;
  }
}

@media screen and (max-width: 1266px) {
  .aside-menu-button {
    transform: translate(0%, -50%);
  }

  .open-aside-menu .aside-menu-button {
    transform: translate(100%, -50%);
  }

  .header__close-button {
    display: block;
  }

  .open-aside-menu .header__close-button:after,
  .open-aside-menu .header__close-button:before {
    width: 40px;
  }

  .main__content {
    max-width: 680px;
  }

  .main__aside {
    position: fixed;
    top: 56px;
    transform: translateX(100%);
    transition: transform 0.6s, opacity 0.6s;
    height: calc(100vh - 56px);
    width: 348px;
    right: 0;
    overflow-y: auto;
    background-color: var(--bg-color);
    padding: var(--gap) calc(var(--gap) * 0.5);
    opacity: 0;
    z-index: 10;
  }

  .open-aside-menu .main__aside {
    width: 348px;
    opacity: 1;
    transform: translateX(0%);
    box-shadow: var(--box-shadow);
  }

  .open-aside-menu {
    overflow: hidden;
  }
}

@media screen and (max-width: 1024px) {
  .filters {
    flex-direction: column;
    height: auto;
    align-items: flex-start;
    margin: 0 0 var(--gap);
  }

  .filters__col {
    align-self: stretch;
    margin: 0;
  }

  .filters__col--315 {
    flex: 0 0 auto;
    margin-bottom: var(--gap);
  }

  .white-table {
    display: block;
  }

  .white-table__row {
    display: flex;
    flex-wrap: wrap;
  }

  .white-table__row--title {
    display: none;
  }

  .white-table__cell {
    display: block;
    flex: 1 1 50%;
    border-bottom: none;
  }
}

@media screen and (max-width: 768px) {
  .aside {
    display: none;
  }

  .cards {
    justify-content: center;
    margin: 0 -4px;
  }

  .cards__item {
    margin: 0 calc(var(--gap) * 0.5) calc(var(--gap) * 1.5);
  }

  .container--main {
    padding: 94px calc(var(--gap) * 0.5) calc(var(--gap) * 9);
  }

  .header {
    padding: calc(var(--gap) * 1.5) 0;
  }

  .header__container {
    align-items: flex-end;
    padding-left: calc(var(--gap) * 2);
  }

  .header__logo {
    margin-bottom: calc(var(--gap) * 0.5);
