.white-table {
  display: table;
  width: 100%;
  background-color: var(--white-color);
  box-shadow: 0px 5px 8px rgba(0, 0, 0, 0.04);
  border-radius: var(--border-radius);
  padding: 16px;

  @include laptop() {
    display: block;
  }

  &__row {
    display: table-row;

    @include laptop() {
      display: flex;
      flex-wrap: wrap;
    }

    @include ipad() {
      margin-bottom: 8px;
    }

    &--title {
      @include laptop() {
        display: none;
      }
      .white-table {
        &__cell {
          border-bottom: none;
          padding: 2px 4px;
        }
      }
    }
  }

  &__cell {
    display: table-cell;
    vertical-align: top;
    padding: 10px 4px;
    border-bottom: 1px solid var(--border-color);

    @include laptop() {
      display: block;
      flex: 1 1 50%;
      border-bottom: none;
    }

    @include ipad() {
      flex: 1 1 100%;
      padding: 8px 4px;
    }

    &--employee {
      width: 292px;
    }

    &--department {
      width: 224px;

      @include ipad() {
        padding-left: 54px;
      }
    }

    &--sity {
      width: 176px;
      @include ipad() {
        padding-left: 54px;
      }
    }

    &--contacts {
      width: 224px;
      @include ipad() {
        padding-left: 54px;
      }
    }
  }

  &__title {
    display: flex;
    align-items: center;
    font-weight: 600;
    font-size: 15px;
    line-height: 16px;

    &.active {
      color: var(--main-color-2);

      .white-table {
        &__sort-arrow {
          fill: rgba(17, 32, 46, 0.2);

          &.active {
            fill: var(--main-color-2);
          }
        }
      }
    }
  }

  &__sort {
    display: inline-flex;
    align-items: center;
    height: 18px;
    width: 20px;
    margin-left: 10px;
  }

  &__sort-arrow {
    width: 6px;
    height: 15px;
    margin-right: 2px;
    fill: rgba(17, 32, 46, 0.8);

    &:hover {
      fill: var(--main-color-2);
    }
  }

  &__cell-title {
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
  }

  &__text {
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.1px;
    color: var(--text-light-color);
  }
}
