.count {
  display: flex;
  align-items: center;
  font-weight: 500;
  font-size: 14px;
  line-height: 24px;
  color: var(--text-color-2);
  white-space: nowrap;

  @include phone() {
    flex-direction: column;
    align-items: flex-start;
    line-height: 18px;
  }

  span {
    font-weight: 600;
  }
}
