.link {
  display: inline-flex;
  align-items: center;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: var(--text-color);

  &:link {
    color: var(--main-color-2);
  }
  &__icon {
    width: 16px;
    height: 16px;
    fill: var(--text-color);
    margin-right: 4px;
  }
}
