$custom-select-block: "custom-select" !default;

.#{$custom-select-block} {
  position: relative;

  &__option {
    overflow: hidden;
    box-sizing: border-box;
    display: block;
    width: 100%;
    padding: 0;
    background-color: transparent;
    border: 0;
    border-radius: 0;
    font-family: inherit;
    white-space: nowrap;
    text-align: left;
    text-overflow: ellipsis;
    cursor: pointer;
    user-select: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
  }

  &__option-wrap {
    position: relative;
  }

  &__input {
    box-sizing: border-box;
    display: block;
    width: 100%;
    padding: 0;
    border-width: 1px 0;
    border-style: solid;
    border-radius: 0;
    font-family: inherit;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
  }

  &__dropdown {
    position: absolute;
    box-sizing: border-box;
    width: 100%;
    top: 100%;
    left: 0;

    .#{$custom-select-block}--dropup & {
      top: auto;
      bottom: 100%;
    }
  }
}

.custom-select {
  display: flex;
  align-items: center;

  &--active {
    .custom-select {
      &__option--value {
        border-color: var(--main-color-2);

        &:after {
          background-image: url("../../img/icons/CaretUp.svg");
        }
      }
    }
  }

  &__dropdown {
    min-width: 248px;
    padding: var(--gap);
    border: 1px solid var(--border-color);
    border-radius: var(--border-radius-small);
    box-shadow: 0px 5px 8px rgba(0, 0, 0, 0.04);
    background-color: var(--white-color);
    margin-top: 4px;
    z-index: 10;
  }

  &__option {
    min-height: 36px;
    padding: var(--gap) calc(var(--gap) * 2);
    color: var(--text-color-2);
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    white-space: normal;
  }

  &__option--value {
    position: relative;
    height: 40px;
    padding: var(--gap) calc(var(--gap) * 4) var(--gap) calc(var(--gap) * 2);
    border: 1px solid var(--light-gray-color);
    border-radius: var(--border-radius-small);
    background-color: var(--white-color);
    position: relative;
    z-index: 1;
    color: var(--text-light-color);
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;

    &:after {
      position: absolute;
      right: 16px;
      top: 50%;
      transform: translateY(-50%);
      width: 16px;
      height: 16px;
      content: "";
      background-size: 100%;
      background-image: url("../../img/icons/CaretDown.svg");
    }
  }
}
