.input {
  display: flex;
  position: relative;
  width: 100%;
  height: 40px;

  &__field {
    width: 100%;
    height: 100%;
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    padding: 10px 16px;
    outline: none;
    border-radius: 8px;
    background-color: var(--white-color);
    border: 1px solid var(--border-color);
    transition: border-color 0.3s;

    &:focus {
      border-color: var(--main-color);
    }
  }

  &__icon {
    position: absolute;
    top: 50%;
    right: 16px;
    transform: translate(0, -50%);
    width: 16px;
    height: 16px;
    display: none;
    cursor: pointer;

    &.active {
      display: block;
    }
  }
}
