.filter-button {
  display: flex;

  &__field {
    position: relative;
    display: flex;
    align-items: center;
    padding: var(--gap) calc(var(--gap) * 2);
    border-radius: 8px 8px 0 0;
    height: 40px;
    @include phone() {
      padding: var(--gap);
    }

    &.active {
      background-color: var(--white-color);

      &:after {
        content: "";
        position: absolute;
        left: 0;
        right: 0;
        top: 100%;
        background-color: var(--white-color);
        height: 20px;
      }

      .filter-button {
        &__icon {
          fill: var(--main-color);
        }

        &__count {
          display: flex;
        }
      }
    }
  }

  &__icon {
    width: 24px;
    height: 24px;
    fill: rgba(17, 32, 46, 0.8);
    margin-right: var(--gap);
  }

  &__text {
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
  }

  &__count {
    display: none;
    justify-content: center;
    align-items: center;
    padding: 2px;
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    background-color: var(--bg-color);
    border-radius: var(--border-radius-small);
    margin-left: var(--gap);
    color: rgba(17, 32, 46, 0.6);
    min-width: 24px;
    height: 24px;
  }

  &__reset {
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    padding: 0 calc(var(--gap) * 2);
    color: var(--warning-color);
  }
}
